.portfolio {
    width: 100%;
    z-index: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;

    .slider {
        width: 350px;
        height: 100%;
        text-align: center;

        div {
            position: relative;

            img {
                position: relative;
                width: 100%;
                height: 100%;
            }
        }
    }
}