* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App {
    border: 1px solid black;
}

.background {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

header {
    width: 100%;
    height: 100px;
    position: relative;
    box-shadow: 0px 1px 5px 0px rgba(181, 181, 181, .6);
    background-color: #1F1F1F;

    .header__inner {
        width: 100%;
        height: 100%;
        position: relative;

        display: flex;
        justify-content: center;
        align-items: center;

        .logo {
            font-size: 30px;
            font-weight: 700;
            color: #B5B5B5;

            font-family: 'Quando', serif;
        }

        a {
            margin: 0 30px;
            padding: 5px 0;
            text-decoration: none;
            color: #B5B5B5;
            font-size: 18px;
            font-family: 'Quando', serif;
            transition: all .1s;
            position: relative;

            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                background-color: #b5b5b5;
                width: 0;
                height: 2px;
                transition: all .3s;

            }

            &:hover {
                opacity: 0.8;
                transition: all .1s;

                &::after {
                    width: 100%;
                    transition: all .3s;
                }
            }
        }

        a:nth-child(1), a:nth-child(2) {
            &::after {
                left: 0;
            }
        }

        a:nth-child(4), a:nth-child(5) {
            &::after {
                right: 0;
            }
        }
    }
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    box-shadow: 0px -1px 5px 0px rgba(181, 181, 181, .6);
    background-color: #1F1F1F;

    .footer__inner {
        position: relative;
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;

        .copyrights {
            color: #B5B5B5;
            font-size: 20px;
            font-family: 'Quando', serif;
        }

        .insta {
            color: #B5B5B5;
        }
    }
}
